<template>
	<view class="content">
		<scroll-view class="_content" :scroll-y="true">
			<view class="_lists">
				<image class="header_img" :src="test_img" mode="aspectFill"></image>
				<view class="_right">
					<view class="user_name">
						<text>{{moving_details.name}}</text>
						<text class="guanzhu">关注</text>
					</view>
					<text class="text_content">{{moving_details.content}}</text>
					<!-- 图片 -->
					<image v-if="moving_details.imageslist.length < 2 && moving_details.imageslist.length !== 0" class="big_images" :src="moving_details.imageslist[0]" mode="aspectFill"></image>
					<view class="img_content" v-if="moving_details.imageslist.length > 1">
						<image class="images" v-for="(m, index1) in moving_details.imageslist" :key="index1" :src="m" mode="aspectFill"></image>
					</view>
					<text class="time_content">{{moving_details.timeout}}</text>
					<view class="bottom_tab">
						<view class="content_event">
							<i class="iconfont">&#xe755;</i>
							<i class="iconfont">&#xe65f;</i>
						</view>
					</view>
				</view>
			</view>
			<view class="_lists other_lists" v-for="(n, index) in comment_list" :key="index">
				<image class="header_img" :src="test_img" mode="aspectFill"></image>
				<view class="_right">
					<view class="user_name">
						<text>{{n.name}}</text>
						<i @click="like(index)" class="iconfont">{{n.isdianzan ? '&#xe685;' : '&#xe60a;'}}</i>
					</view>
					<text class="text_content">{{n.content}}</text>
				</view>
			</view>
		</scroll-view>
		<view class="_input">
			<input class="uni-input" confirm-type="send" v-model="comment" placeholder="说些什么吧" @confirm="go_comment"/>
		</view>
	</view>
</template>

<script>
	import test_img from '../../static/img4.jpg'
	export default {
		data() {
			return {
				sayPicList: [test_img],
				test_img: test_img,
				is_like: 0,
				moving_id: '',
				moving_details: '',
				pageNum: 1,
				comment_list: [],
				comment:'',
			}
		},
		onShow() {
			
		},
		onLoad(option){
			this.moving_id = option.id
			this.init()
			this.getcomments()
		},
		methods: {
			go_comment () {
				this.$http
				.post('/api/addcomments', 
				{
					userid: uni.getStorageSync('userId'),
					id: this.moving_id,
					content: this.comment
				}, {})
				.then(res => {
					console.log(res.data)
					this.comment = ''
				})
				.catch(err => {
				})
			},
			init () {
				this.$http
				.post('/api/getdynamic', 
				{
					userid: uni.getStorageSync('userId'),
					id: this.moving_id
				}, {})
				.then(res => {
					console.log(res.data)
					this.moving_details = res.data
					uni.setNavigationBarTitle({
					    title: this.moving_details.name
					});
				})
				.catch(err => {
				})
			},
			getcomments () {
				this.$http
				.post('/api/getcomments', 
				{
					userid: uni.getStorageSync('userId'),
					id: this.moving_id,
					pageNum: this.pageNum,
					pageSize: 5
				}, {})
				.then(res => {
					this.comment_list = res.data.rows
					console.log(res.data)
				})
				.catch(err => {
				})
			},
			like (n) {
				this.$http
				.post('/api/commentspraise', 
				{
					userid: uni.getStorageSync('userId'),
					id: this.comment_list[n].id
				}, {})
				.then(res => {
					this.comment_list[n].isdianzan = !this.comment_list[n].isdianzan
				})
				.catch(err => {
				})
			}
		}
	}
</script>

